import Vue from 'vue';
import i18n from '../../../../../i18n';
export default Vue.extend({
    i18n: i18n('common/views/components/games/reversi/reversi.index.vue'),
    data() {
        return {
            games: [],
            gamesFetching: true,
            gamesMoreFetching: false,
            myGames: [],
            matching: null,
            invitations: [],
            connection: null
        };
    },
    mounted() {
        if (this.$store.getters.isSignedIn) {
            this.connection = this.$root.stream.useSharedConnection('gamesReversi');
            this.connection.on('invited', this.onInvited);
            this.$root.api('games/reversi/games', {
                my: true
            }).then(games => {
                this.myGames = games;
            });
            this.$root.api('games/reversi/invitations').then(invitations => {
                this.invitations = this.invitations.concat(invitations);
            });
        }
        this.$root.api('games/reversi/games').then(games => {
            this.games = games;
            this.gamesFetching = false;
        });
    },
    beforeDestroy() {
        if (this.connection) {
            this.connection.dispose();
        }
    },
    methods: {
        go(game) {
            this.$emit('go', game);
        },
        async match() {
            const { result: user } = await this.$root.dialog({
                title: this.$t('enter-username'),
                user: {
                    local: true
                }
            });
            if (user == null)
                return;
            this.$root.api('games/reversi/match', {
                userId: user.id
            }).then(res => {
                if (res == null) {
                    this.$emit('matching', user);
                }
                else {
                    this.$emit('go', res);
                }
            });
        },
        accept(invitation) {
            this.$root.api('games/reversi/match', {
                userId: invitation.parent.id
            }).then(game => {
                if (game) {
                    this.$emit('go', game);
                }
            });
        },
        onInvited(invite) {
            this.invitations.unshift(invite);
        }
    }
});
