import Vue from 'vue';
import i18n from '../../../../../i18n';
import XGameroom from './reversi.gameroom.vue';
import XIndex from './reversi.index.vue';
import Progress from '../../../../scripts/loading';
export default Vue.extend({
    i18n: i18n('common/views/components/games/reversi/reversi.vue'),
    components: {
        XGameroom,
        XIndex
    },
    props: {
        gameId: {
            type: String,
            required: false
        },
        selfNav: {
            type: Boolean,
            require: false,
            default: true
        }
    },
    data() {
        return {
            game: null,
            matching: null,
            connection: null,
            pingClock: null
        };
    },
    watch: {
        game() {
            this.$emit('gamed', this.game);
        },
        gameId() {
            this.fetch();
        }
    },
    mounted() {
        this.fetch();
        if (this.$store.getters.isSignedIn) {
            this.connection = this.$root.stream.useSharedConnection('gamesReversi');
            this.connection.on('matched', this.onMatched);
            this.pingClock = setInterval(() => {
                if (this.matching) {
                    this.connection.send('ping', {
                        id: this.matching.id
                    });
                }
            }, 3000);
        }
    },
    beforeDestroy() {
        if (this.connection) {
            this.connection.dispose();
            clearInterval(this.pingClock);
        }
    },
    methods: {
        fetch() {
            if (this.gameId == null) {
                this.game = null;
            }
            else {
                Progress.start();
                this.$root.api('games/reversi/games/show', {
                    gameId: this.gameId
                }).then(game => {
                    this.game = game;
                    Progress.done();
                });
            }
        },
        async nav(game, actualNav = true) {
            if (this.selfNav) {
                // 
                if (game != null && game.map == null) {
                    game = await this.$root.api('games/reversi/games/show', {
                        gameId: game.id
                    });
                }
                this.game = game;
            }
            else {
                this.$emit('nav', game, actualNav);
            }
        },
        onMatching(user) {
            this.matching = user;
        },
        cancel() {
            this.matching = null;
            this.$root.api('games/reversi/match/cancel');
        },
        accept(invitation) {
            this.$root.api('games/reversi/match', {
                userId: invitation.parent.id
            }).then(game => {
                if (game) {
                    this.matching = null;
                    this.nav(game);
                }
            });
        },
        onMatched(game) {
            this.matching = null;
            this.game = game;
            this.nav(game, false);
        },
        goIndex() {
            this.nav(null);
        }
    }
});
